<template>
    <div class="container-fluid">
        <div class="row head">
            <div class="col-lg-6 col-md-6 col-sm-12 col-12 buttons">
                <v-btn class="t-btn-select" :plain="!button.active" @click="activeButton(button.key)" large
                    v-for="button in buttons" :key="button.key">{{ button.text }} ({{ button.quantity }})
                </v-btn>
            </div>
            <div class="col-lg-6 col-md-6 col-sm-12 col-12 switch">
                <h2>Auto</h2>
                <v-switch v-model="status" color="success" hide-details></v-switch>
            </div>
        </div>

        <div class="row">
            <div class="col-12">
                <table class="table">
                    <tr v-for="(o, key) in filterOrders" :key="key">
                        <td>
                            <div class="t-user">
                                <img src="@/assets/Images/Vector.png" alt="" />
                                <div>
                                    <h3>{{ o.numOrder }}</h3>
                                    <p>{{ o.nameUser }}</p>
                                </div>
                            </div>
                        </td>
                        <td class="t-restaurant">
                            <img src="@/assets/Images/img-placeholder.png" width="60" alt="restaurant-logo" />
                            <p>{{ o.restaurant }}</p>
                        </td>
                        <td>
                            <div class="t-detail">
                                <p>{{ o.quantity }}</p>
                                <a @click="toDetail(o)">{{ o.detail }}</a>
                            </div>
                        </td>
                        <td class="t-hour">
                            {{ o.hour }}
                        </td>
                        <td>
                            <v-btn class="t-button" v-if="o.status!=0">
                                Imprimir Ticket
                            </v-btn>
                            <div class="d-flex justify-content-around t-options" v-if="o.status===0">
                                <v-btn color="#26E822" dark @click="dialog = !dialog">Aceptar</v-btn>
                                <v-btn color="white" light>Cancelar</v-btn>
                            </div>
                        </td>
                    </tr>
                </table>
            </div>
        </div>

        <v-row class="text-left" id="buttons">
            <v-col cols="12" sm="6">
                <div class="text-left">
                    <div class="my-1">
                        <v-btn color="red" fab large dark top left>
                            <v-icon>mdi-message-outline</v-icon>
                        </v-btn>
                    </div>
                </div>
            </v-col>
        </v-row>


        <template>
            <v-row justify="center">
                <v-dialog v-model="dialog" persistent max-width="620">
                    <v-card>

                        <div class="container">
                            <div class="">
                                <v-btn class="ma-2" text icon outlined color="black lighten-2" @click="minutes-=5">
                                    <v-icon>mdi-minus</v-icon>
                                </v-btn>

                                <p class="d-inline">{{minutes}} min</p>

                                <v-btn class="ma-2" text icon outlined color="black lighten-2" @click="minutes+=5">
                                    <v-icon>mdi-plus</v-icon>
                                </v-btn>
                            </div>

                            <div class="row">
                                <div class="col-6">
                                    <v-btn color="#E4442E" block dark @click="dialog = !dialog">Aceptar</v-btn>
                                </div>

                                <div class="col-6">
                                    <v-btn color="white" block light @click="dialog = !dialog">Cancelar</v-btn>
                                </div>

                            </div>
                        </div>
                    </v-card>
                </v-dialog>
            </v-row>
        </template>

    </div>

</template>

<script>
    export default {
        data() {
            return {
                minutes: 5,
                dialog: false,
                orders: [{
                        detail: "Salchipapa, Gaseo...",
                        hour: "09:16 Pm",
                        nameUser: "Carlos Duarte",
                        restaurant: "Restaurante 0",
                        numOrder: "#128332052",
                        quantity: 2,
                        status: 0,
                    },
                    {
                        detail: "Salchipapa, Gaseo...",
                        hour: "09:16 Pm",
                        nameUser: "Martha Rodriguez",
                        restaurant: "Restaurante 1",
                        numOrder: "#129332052",
                        quantity: 1,
                        status: 0,
                    },
                    {
                        detail: "Salchipapa, Gaseo...",
                        hour: "09:16 Pm",
                        nameUser: "Martha Rodriguez",
                        restaurant: "Restaurante 1",
                        numOrder: "#129332052",
                        quantity: 2,
                        status: 1,
                    },
                    {
                        detail: "Salchipapa, Gaseo...",
                        hour: "09:16 Pm",
                        nameUser: "Martha Rodriguez",
                        restaurant: "Restaurante 1",
                        numOrder: "#129332052",
                        quantity: 2,
                        status: 2,
                    },
                    {
                        detail: "Salchipapa, Gaseo...",
                        hour: "09:16 Pm",
                        nameUser: "Martha Rodriguez",
                        restaurant: "Restaurante 1",
                        numOrder: "#129332052",
                        quantity: 2,
                        status: 2,
                    },
                ],
                buttons: [{
                        key: 0,
                        text: "Peticiones",
                        active: true,
                        quantity: 3,
                    },
                    {
                        key: 1,
                        text: "Preparando",
                        active: false,
                        quantity: 2,
                    },
                    {
                        key: 2,
                        text: "Completado",
                        active: false,
                        quantity: 2,
                    }
                ],
                status: false,
                type: 0,
            };
        },
        computed: {
            filterOrders() {
                return this.orders.filter((order) => {
                    return order.status == this.type;
                });
            },
        },
        created() {
            this.$store.state.title = "Órdenes";
            for (const item of this.buttons) {
                item.quantity = this.orders.filter((order) => {
                    return order.status == item.key;
                }).length;
            }
        },
        methods: {
            activeButton(key) {
                for (const item of this.buttons) {
                    if (key === item.key) {
                        item.active = true;
                    } else {
                        item.active = false;
                    }
                }
                this.type = key;
            },
            toDetail() {
                this.$router.push("/order");
            },
            toSelectDomi() {
                this.$router.push({
                    name: 'SelectDomi',
                    params: {
                        id: 45
                    }
                });
            }
        },
    };
</script>

<style scoped>
    .container-fluid {
        padding-left: 122px;
        padding-right: 102px;
        padding-top: 35px;
    }

    td {
        vertical-align: middle;
    }

    table {
        border: 1px solid #eaeaea;
        border-radius: 0px 0px 15px 15px;
    }

    h2 {
        font: normal normal bold 20px/27px Noto Sans;
        color: #626262;
    }

    h3 {
        text-align: left;
        font: normal normal 400 18px/25px Noto Sans;
        color: #6b6b6b;
    }

    p,
    a {
        font: normal normal bold 16px/22px Noto Sans;
        color: #000000;
        margin-bottom: 0;
    }

    a {
        text-decoration: underline;
    }

    .t-detail p {
        width: 28px;
