        border-radius: 50%;
    }

    .t-user,
    .switch,
    .t-detail,
    .buttons {
        display: flex;
        align-items: center;
    }

    .switch {
        justify-content: flex-end;
    }

    .t-detail {
        justify-content: center;
    }

    .buttons {
        justify-content: flex-start;
    }

    .switch h2 {
        margin-bottom: 0;
    }

    .t-user img {
        margin-right: 21px;
        margin-left: 30px;
    }

