        font: normal normal 400 15px/20px Noto Sans;
        color: #6b6b6b;
    }

    .t-button {
        font: normal normal bold 16px/22px Noto Sans;
        color: black;
        background-color: white;
        border-radius: 10px;
        height: 42px;
        width: 200px;
        text-transform: none;
    }

    .head {
        margin-top: 30px;
        margin-bottom: 27px;
    }

    .head>>>.v-input--switch__thumb {
        color: #ffffff !important;
    }

    .head>>>.v-input--switch__track {
        color: #26e822 !important;
        opacity: 1;
    }

    .head>>>.v-input--switch__track.success--text {
        color: #ef3d4c !important;
    }

    .head>>>.v-input--selection-controls {
        margin-top: 0;
        padding-top: 0;
        margin-left: 24px;
    }

    .t-btn-select {
        font: normal normal bold 20px/27px Noto Sans;
        color: #a0a0a0 !important;
        letter-spacing: 0.44px;
        text-transform: capitalize;
        margin-right: 10px;
        height: 51px !important;
    }

    .t-btn-select>>>.v-btn__content {
        opacity: 1 !important;
    }

    .t-options>>>.v-btn {
        text-transform: none;
        padding: 0px 30px;

    }

    .t-options>>>.v-btn--is-elevated {
        box-shadow: none;
    }

    .buttons>>>.v-btn--has-bg {
        background: #ffffff;
        box-shadow: 0px 4px 15px rgba(133, 133, 133, 0.15);
        border-radius: 10px;
        color: #626262 !important;
    }

    #buttons {
        bottom: 0;
        left: 0;
        position: absolute;
        margin: 0 0 67px 61px;
    }

    #buttons>>>.v-btn--fab.v-size--large {
        height: 75px;
        width: 75px;
        background: #e4442e;
        box-shadow: 0px 0px 30px rgba(255, 95, 64, 0.2);
    }
</style>